}

.see-all {
  font-family: Poppins, sans-serif;
  font-size: 1em;
  font-weight: 600;
}

.capaitalised {
  text-transform: uppercase;
  color: #f59e00;
  font-family: Poppins, sans-serif;
  font-size: 1em;
  font-weight: 600;
}

.categories__category__cover, .sound-effect-navigation__more, .sound-effect-navigation__download, .sound-effect-navigation__like {
  justify-content: center;
