  display: flex;
}

html {
  min-width: 320px;
}

body {
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.7;
}

body * {
  margin: 0;
}

.see-all {
  color: #0432df;
}

.see-all:hover {
  text-decoration: none;
}

.container {
  max-width: 1044px;
  margin: auto;
}

@media (max-width: 1200px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}

@media (max-width: 700px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.columns {
  display: flex;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #0432df;
  border: 0;
  border-radius: 8px;
  padding: 14px 24px;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
}

.button:hover {
  background-color: #0029c2;
  box-shadow: 0 10px 12px #0432df1f;
}

.button:active {
  background-color: #0432df;
}

@media (max-width: 1200px) {
  .button {
    padding: 10px 20px;
    font-size: 1em;
  }
}

.button_outline {
  background: none;
  border: 2px solid #fff;
  padding: 12px 22px;
}

@media (max-width: 1200px) {
  .button_outline {
    padding: 8px 18px;
  }
}

.play-button {
  width: 34px;
  height: 34px;
  cursor: pointer;
  background-color: #cdd6f9;
  border: 0;
  border-radius: 50%;
  margin: 0;
  padding: 0;
  font-size: 0;
  transition: all .2s ease-out;
  display: inline-block;
}

.play-button:hover {
  box-shadow: 0 0 0 5px #cdd6f980;
}

.play-button:active {
  box-shadow: 0 0 0 2px #cdd6f980;
}

.play-button__img {
  margin-left: 12px;
  display: block;
}

.sound-effect-navigation {
  column-gap: 20px;
  display: flex;
}

@media (max-width: 700px) {
  .sound-effect-navigation {
    column-gap: 10px;
  }
}

.sound-effect-navigation__like {
  width: 36px;
  height: 36px;
  cursor: pointer;
  border-radius: 4px;
  transition: all .1s;
}

.sound-effect-navigation__like:hover {
  background-color: #f1f1f1;
}

.sound-effect-navigation__like:active {
  background-color: #ddd;
}

@media (max-width: 700px) {
  .sound-effect-navigation__like {
    display: none;
  }
}

.sound-effect-navigation__download {
  width: 36px;
  height: 36px;
  border-radius: 4px;
  font-size: 0;
  text-decoration: none;
  transition: all .1s;
}

.sound-effect-navigation__download:hover {
  background-color: #f1f1f1;
}

.sound-effect-navigation__download:active {
  background-color: #ddd;
}

.sound-effect-navigation__more {
  width: 36px;
  height: 36px;
  cursor: pointer;
  background: none;
  border: 0;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  font-size: 0;
  transition: all .1s;
}

.sound-effect-navigation__more:hover {
  background-color: #f1f1f1;
}

.sound-effect-navigation__more:active {
  background-color: #ddd;
}

.sound-effect-navigation__more__dot {
  margin: auto;
  position: relative;
}

.sound-effect-navigation__more__dot, .sound-effect-navigation__more__dot:after, .sound-effect-navigation__more__dot:before {
  width: 4px;
  height: 4px;
  background-color: #4c526a;
  border-radius: 50%;
  display: block;
}

.sound-effect-navigation__more__dot:after, .sound-effect-navigation__more__dot:before {
  content: "";
  position: absolute;
  top: 0;
}

.sound-effect-navigation__more__dot:before {
  left: -7px;
}

.sound-effect-navigation__more__dot:after {
  right: -7px;
}

[name="sound-effects"] {
  display: none;
}

[name="sound-effects"]:checked + .sound-effect-navigation__like path {
  stroke: #0432df;
  fill: #cdd6f9;
}

.header {
  z-index: 1;
  position: relative;
}

@media (max-width: 1200px) {
  .header {
    background-color: #020d37;
    position: sticky;
    top: 0;
  }
}

.header:after {
  content: "";
  width: 100%;
  height: 1px;
  opacity: .1;
  background-color: #fff;
  display: block;
}

.header > .container {
  height: 84px;
  align-items: center;
}

@media (max-width: 1200px) {
  .header > .container {
    height: 70px;
  }
}

.header__logo {
  flex: 1;
}

.header__logo__img {
  width: 125px;
}

.header__menu {
  align-items: center;
  transition: all .4s;
  display: flex;
}

@media (max-width: 1200px) {
  .header__menu {
    height: 0;
    width: 100%;
    background-color: #020d37;
    flex-direction: column;
    position: absolute;
    top: 70px;
    left: 0;
    overflow: hidden;
  }
}

.header__menu__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

@media (max-width: 1200px) {
  .header__menu__list {
    width: calc(100% - 120px);
    flex-direction: column;
    margin-top: 25px;
    padding-left: 60px;
    padding-right: 60px;
  }
}

.header__menu__list-item {
  margin-left: 35px;
}

@media (max-width: 1200px) {
  .header__menu__list-item {
    text-align: center;
    margin-left: 0;
  }
}

.header__menu__link {
  color: #fff;
  opacity: .6;
  font-family: Poppins, sans-serif;
  text-decoration: none;
  transition: all .1s;
}

.header__menu__link:hover {
  opacity: 1;
}

@media (max-width: 1200px) {
  .header__menu__link {
    line-height: 50px;
    display: block;
  }
}

.header__menu__link_active {
  opacity: 1;
}

.header__button {
  margin-left: 40px;
  padding: 10px 20px;
  font-size: 1em;
}

@media (max-width: 1200px) {
  .header__button {
    margin: 25px 0 0;
  }
}

.header__menu-toggle {
  width: 50px;
  height: 50px;
  cursor: pointer;
  display: none;
  position: relative;
}

@media (max-width: 1200px) {
  .header__menu-toggle {
    display: block;
  }
}

.header__menu-toggle__line {
  transition-delay: .1s;
}

.header__menu-toggle__line, .header__menu-toggle__line:before, .header__menu-toggle__line:after {
  content: "";
  width: 30px;
  height: 3px;
  background-color: #eee;
  border-radius: 1.5px;
  display: block;
  position: absolute;
  top: calc(50% - 1.5px);
  left: calc(50% - 15px);
}

.header__menu-toggle__line:before {
  margin-top: -9px;
  transition: transform .1s ease-in-out, margin .1s ease-in-out .1s;
}

.header__menu-toggle__line:after {
  margin-top: 9px;
  transition: transform .1s ease-in-out, margin .1s ease-in-out .1s;
}

.header-checkbox {
  display: none;
}

@media (max-width: 1200px) {
  .header-checkbox:checked ~ .section, .header-checkbox:checked ~ .footer {
    display: none;
  }

  .header-checkbox:checked ~ .header .header__menu {
    height: calc(100vh - 70px);
  }

  .header-checkbox:checked ~ .header .header__menu-toggle__line {
    background-color: #0000;
  }

  .header-checkbox:checked ~ .header .header__menu-toggle__line:before {
    margin: 0;
    transition: margin .1s ease-in-out, transform .1s ease-in-out .1s;
    transform: rotate(45deg);
  }

  .header-checkbox:checked ~ .header .header__menu-toggle__line:after {
    margin: 0;
    transition: margin .1s ease-in-out, transform .1s ease-in-out .1s;
    transform: rotate(-45deg);
  }
}

.banner {
  color: #eee;
  text-align: center;
  background-color: #0000;
  background-image: linear-gradient(to top, #020d37b3, #020d37b3), url("banner.ba1b3a34.webp");
  background-position: center;
  background-repeat: repeat, repeat;
  background-size: auto, auto;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  margin-top: -85px;
  padding: 180px 0 145px;
}

@media (max-width: 1200px) {
  .banner {
    padding-bottom: 125px;
  }
}

@media (max-width: 700px) {
  .banner {
    padding: 125px 0 100px;
  }
}

.banner__title {
  margin-bottom: 15px;
}

@media (max-width: 700px) {
  .banner__title {
    font-size: 1.43em;
    font-weight: 600;
  }
}

.banner__subtitle {
  color: #acafb8;
  margin: 0 0 50px;
}

@media (max-width: 1200px) {
  .banner__subtitle {
    font-size: 1.28em;
  }
}

@media (max-width: 700px) {
  .banner__subtitle {
    font-size: 1.14em;
    font-weight: 600;
  }
}

.banner__search {
  width: 100%;
  max-width: 600px;
  margin: 0 auto 50px;
}

.banner__search__input {
  width: calc(100% - 70px);
  background-color: #eee;
  background-image: url("search-icon.83b1b532.svg");
  background-position: 20px;
  background-repeat: no-repeat;
  border: 0;
  border-radius: 8px;
  outline: none;
  margin-bottom: 10px;
  padding: 15px 15px 15px 55px;
}

.banner__search__input:focus {
  box-shadow: 0 0 0 2px #0432df;
}

@media (max-width: 700px) {
  .banner__search__input {
    background-position: 15px;
    margin-bottom: 15px;
    padding-left: 50px;
  }
}

.banner__search__input::placeholder {
  color: #4c526a;
}

.banner__search__description {
  color: #eee;
  text-align: left;
}

.banner__description, .banner__description__link {
  color: #eee;
}

.banner__description__link:hover {
  text-decoration: none;
}

.trusted {
  background-color: #fafafa;
  padding: 50px 0 60px;
}

@media (max-width: 1200px) {
  .trusted {
    padding: 40px 0;
  }
}

@media (max-width: 700px) {
  .trusted {
    padding-top: 30px;
  }
}

.trusted__title {
  text-align: center;
  color: #020d37;
  margin-bottom: 35px;
}

@media (max-width: 700px) {
  .trusted__title {
    font-size: 1.28em;
  }
}

.trusted__list {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (max-width: 700px) {
  .trusted__list {
    gap: 40px 7%;
  }
}

@media (max-width: 1200px) {
  .trusted__list-item__img {
    max-height: 40px;
    max-width: 90px;
  }
}

.sound-effects {
  padding: 80px 0 60px;
}

@media (max-width: 1200px) {
  .sound-effects {
    padding: 50px 0;
  }
}

.sound-effects [name="cat"] {
  display: none;
}

.sound-effects__title {
  margin-bottom: 15px;
}

@media (max-width: 1200px) {
  .sound-effects__title {
    font-size: 1.71em;
  }
}

@media (max-width: 700px) {
  .sound-effects__title {
    font-size: 1.43em;
  }
}

.sound-effects__navigation {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 3px 0;
  display: flex;
  position: relative;
}

@media (max-width: 1200px) {
  .sound-effects__navigation {
    margin-bottom: 15px;
  }
}

@media (max-width: 600px) {
  .sound-effects__navigation:before, .sound-effects__navigation:after {
    content: "";
    width: 10px;
    height: 10px;
    border-top: 2px solid #acafb8;
    border-right: 2px solid #acafb8;
    margin-top: -5px;
    display: block;
    position: absolute;
    top: 50%;
    right: 24%;
    transform: rotate(45deg);
  }

  .sound-effects__navigation:after {
    margin-right: -8px;
  }
}

.sound-effects__navigation__cats-bg {
  flex: 1;
}

@media (max-width: 700px) {
  .sound-effects__navigation__cats-bg {
    margin-right: 20%;
    padding: 0;
    overflow-x: auto;
  }

  .sound-effects__navigation__cats-bg::-webkit-scrollbar {
    width: 0;
  }
}

.sound-effects__navigation__cats {
  flex-wrap: nowrap;
  padding-bottom: 2px;
  display: flex;
}

.sound-effects__navigation__cat-item {
  color: #020d37;
  cursor: pointer;
  margin-right: 25px;
  transition: all .1s;
  display: inline-block;
  position: relative;
}

.sound-effects__navigation__cat-item:hover {
  color: #0029c2;
}

.sound-effects__navigation__cat-item:hover:after {
  background-color: #0029c2;
}

@media (max-width: 1200px) {
  .sound-effects__navigation__cat-item {
    font-size: 1em;
  }
}

@media (max-width: 700px) {
  .sound-effects__navigation__cat-item {
    margin-right: 15px;
    line-height: 36px;
  }
}

.sound-effects__navigation__cat-item:after {
  background-color: #020d37;
  transition: all .1s;
}

.sound-effects__navigation__filter {
  color: #4c526a;
  cursor: pointer;
  background-color: #f1f1f1;
  border: 0;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 9px 20px;
  line-height: 20px;
  transition: all .2s ease-in-out;
  display: flex;
}

.sound-effects__navigation__filter:hover {
  background-color: #ddd;
}

.sound-effects__navigation__filter:active {
  background-color: #f1f1f1;
}

@media (max-width: 1200px) {
  .sound-effects__navigation__filter {
    padding: 10px 12px;
    font-size: 1em;
  }
}

@media (max-width: 700px) {
  .sound-effects__navigation__filter {
    width: 36px;
    height: 36px;
    padding: 0;
    font-size: 0;
  }
}

.sound-effects__navigation__filter:after {
  content: url("filter.0a865a0f.svg");
  height: 16px;
  margin-left: 24px;
}

@media (max-width: 1200px) {
  .sound-effects__navigation__filter:after {
    margin-left: 20px;
  }
}

@media (max-width: 700px) {
  .sound-effects__navigation__filter:after {
    margin: 0;
  }
}

.sound-effects__playlist {
  margin-bottom: 20px;
}

@media (max-width: 700px) {
  .sound-effects__playlist {
    margin-bottom: 15px;
  }
}

.sound-effects__playlist__item {
  border-bottom: 1px solid #eee;
  align-items: center;
  column-gap: 45px;
  padding: 10px 10px 10px 20px;
