#all:checked ~ .sound-effects__navigation [for="all"]:after, #jumpscare:checked ~ .sound-effects__navigation [for="jumpscare"]:after, #mellow:checked ~ .sound-effects__navigation [for="mellow"]:after, #happiness:checked ~ .sound-effects__navigation [for="happiness"]:after, #ambience:checked ~ .sound-effects__navigation [for="ambience"]:after, #zonk:checked ~ .sound-effects__navigation [for="zonk"]:after {
  content: "";
  width: 100%;
  height: 2px;
  border-radius: 1px;
  display: block;
  position: absolute;
  bottom: -2px;
}

@media (max-width: 700px) {
  #all:checked ~ .sound-effects__navigation [for="all"]:after, #jumpscare:checked ~ .sound-effects__navigation [for="jumpscare"]:after, #mellow:checked ~ .sound-effects__navigation [for="mellow"]:after, #happiness:checked ~ .sound-effects__navigation [for="happiness"]:after, #ambience:checked ~ .sound-effects__navigation [for="ambience"]:after, #zonk:checked ~ .sound-effects__navigation [for="zonk"]:after {
