.categories {
  padding: 60px 0;
}

@media (max-width: 1200px) {
  .categories {
    padding: 40px 0;
  }
}

@media (max-width: 700px) {
  .categories {
    padding: 30px 0 40px;
  }
}

.categories__title {
  margin-bottom: 25px;
}

@media (max-width: 1200px) {
  .categories__title {
    margin-bottom: 20px;
    font-size: 1.71em;
  }
}

@media (max-width: 700px) {
  .categories__title {
    font-size: 1.43em;
  }
}

.categories__grid {
  grid-template-columns: repeat(6, 1fr);
  gap: 15px 20px;
  margin-bottom: 20px;
  display: grid;
}

@media (max-width: 1200px) {
  .categories__grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 10px 15px;
